.slider {
  position: relative;
  width: 100%;
  height: 500px;
}

.slider__bullet {
  display: inline-flex;
  border: 1px solid var(--white-color);
  border-radius: 100%;
  width: 10px;
  height: 10px;
  background-color: var(--white-color);
  transition: background-color .2s var(--transition-main);
  cursor: pointer;

  &--active {
    background-color: transparent;
  }
}

.slider__bullet + .slider__bullet {
  margin-left: 20px;
}