@import url('https://fonts.googleapis.com/css2?family=Play:wght@400;700&family=Roboto:wght@400;500;700&display=swap');

:root {
  --white-color: #FFFFFF;
  --black-color: #000000;
  --muddy-waters: #B5865D;
  --cape-palliser: #996D46;
  --cape-palliser-2: #926843;


  --font-main: Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
  --font-secondary: Play, "Helvetica Neue", Helvetica, Arial, sans-serif;

  --text-shadow: 0 7px 10px #000;
  --transition-main: cubic-bezier(0.767, 0.01, 0.18, 1.01);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}