.slide {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgba(56, 51, 47, 0.3);
  }
}

.slide__content {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  width: 100%;
  text-align: center;
  transform: translate(-50%, -50%);
}

.slide__image {
  width: 100%;
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

.slide__title {
  margin: 0;
  margin-bottom: 10px;
  font-family: var(--font-main);
  font-weight: 700;
  font-size: 56px;
  letter-spacing: 1px;
  line-height: 135%;
  text-transform: uppercase;
  text-align: center;
  color: var(--white-color);
  text-shadow: var(--text-shadow);
}

.slide__subtitle {
  margin: 0;
  margin-bottom: 26px;
  font-family: var(--font-secondary);
  font-weight: 700;
  font-size: 20px;
  line-height: 110%;
  text-shadow: var(--text-shadow);
  text-transform: uppercase;
  text-align: center;
  color: var(--white-color);
}