.button {
  display: inline-block;
  margin: 0;
  border: 1px solid transparent;
  padding: 10px 16px;
  font-family: var(--font-main);
  font-size: 16px;
  line-height: 135%;
  text-transform: uppercase;
  text-align: center;
  color: var(--white-color);
  background-color: var(--muddy-waters);
  cursor: pointer;

  &--primary {
    color: var(--white-color);
    background-color: var(--muddy-waters);
  }

  @media (min-width: 1023.98px) AND (hover) {

    &--primary {
      transition: border .2s var(--transition-main), background-color .2s var(--transition-main);
    }

    &--primary:hover {
      border: 1px solid var(--cape-palliser-2);
      background-color: var(--cape-palliser);
    }
  }
}